'use client';

import { useState } from 'react';
import { Send, Loader2, Check } from 'lucide-react';
import { sendInvoiceEmail } from '@/app/actions/organization-billing';

interface SendInvoiceEmailButtonProps {
  invoiceId: string;
}

export function SendInvoiceEmailButton({ invoiceId }: SendInvoiceEmailButtonProps) {
  const [loading, setLoading] = useState(false);
  const [sent, setSent] = useState(false);
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

  const handleSend = async () => {
    if (!confirm('Send invoice email to organization?')) {
      return;
    }

    setLoading(true);
    setMessage(null);

    try {
      const result = await sendInvoiceEmail({ invoiceId });
      
      if (result.success) {
        setSent(true);
        setMessage({ 
          type: 'success', 
          text: result.message || 'Invoice email sent successfully!' 
        });
        setTimeout(() => {
          setSent(false);
          setMessage(null);
        }, 5000);
      } else {
        setMessage({ type: 'error', text: 'Failed to send email' });
      }
    } catch (error: any) {
      setMessage({ 
        type: 'error', 
        text: error.message || 'Failed to send email' 
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col items-end gap-2">
      <button
        onClick={handleSend}
        disabled={loading || sent}
        className={`flex items-center gap-2 px-4 py-2 rounded-lg transition-colors ${
          sent 
            ? 'bg-green-600 text-white' 
            : 'bg-blue-600 text-white hover:bg-blue-700'
        } disabled:opacity-50 disabled:cursor-not-allowed`}
        title="Send Invoice Email"
      >
        {loading ? (
          <>
            <Loader2 className="h-4 w-4 animate-spin" />
            Sending...
          </>
        ) : sent ? (
          <>
            <Check className="h-4 w-4" />
            Email Sent
          </>
        ) : (
          <>
            <Send className="h-4 w-4" />
            Send Email
          </>
        )}
      </button>
      
      {message && (
        <div
          className={`text-sm px-3 py-2 rounded-lg ${
            message.type === 'success'
              ? 'bg-green-50 text-green-800 border border-green-200'
              : 'bg-red-50 text-red-800 border border-red-200'
          }`}
        >
          {message.text}
        </div>
      )}
    </div>
  );
}
